  display: flex;
  justify-content: space-between;
}
.lesson-form__main {
  border-radius: 10px;
  background: #FFF;
  box-shadow: 0px 10px 60px 0px rgba(42, 102, 193, 0.15);
  max-width: 780px;
  width: 100%;
  padding: 40px 45px 50px;
}

.form-top__name-title {
  color: #8B919E;
  font-family: Montserrat;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  padding-top: 5px;
  padding-bottom: 8px;
}
.form-top__teacher {
  margin-top: 15px;
}

.form-bottom {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 27px;
}
.form-bottom__decor {
  width: 7px;
  height: 1px;
  background-color: #8B919E;
}
.form-bottom__left {
  padding-top: 15px;
  max-width: 330px;
}
.form-bottom__right {
  max-width: 330px;
  padding-top: 15px;
}
.form-bottom__right-age {
  padding-top: 17px;
}
.form-bottom__right-description {
  padding-top: 17px;
}
.form-bottom__right-description textarea {
  border-radius: 20px;
  background: #F2F5FA;
  width: 328px;
  height: 189px;
  padding: 20px;
}
.form-bottom__right-quantity {
  padding-top: 13px;
}
.form-bottom__time-lesson {
  padding-top: 17px;
}
.form-bottom__time-lesson-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form-bottom__time-lesson-time {
  padding-top: 15px;
}
.form-bottom__time-lesson-duration {
  padding-top: 15px;
}
.form-bottom__time-lesson-price {
  padding-top: 17px;
  position: relative;
}
.form-bottom__time-lesson-price img {
  position: absolute;
  top: 55px;
  right: 15px;
  z-index: 2;
}
.form-bottom__time-lesson-quantity {
  padding-top: 17px;
